import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {SharesTransactionPassingComponent} from './shares-transaction-passing.component';

const routes: Routes = [
  {
    path: '',
    component: SharesTransactionPassingComponent,
    data: {
      title: 'Shares Transaction Passing',
    icon: 'icon-home',
    caption: 'lorem ipsum dolor sit amet, consectetur adipisicing elit',
      status: true
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SharesTransactionPassingRoutingModule { }
